}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Founders Grotesk", sans-serif;
}

.container {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
}

.dashboard,
.analytics,
.balances,
.customers,
.constuction {
  display: -webkit-flex;
