          justify-content: space-between;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  padding: 0 0 0 18px;
  line-height: 22px;
  color: #2a2a2a;
  transition: 880ms;
}

.date {
  width: 124px;
  height: 14px;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  color: #a6abb2;
}

.card-container {
  -webkit-flex-direction: row;
          flex-direction: row;
  display: -webkit-flex;
  display: flex;
  max-width: 720px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 60px;
}

.right-menu {
width: 160px;
height: 40px;
display: -webkit-flex;
display: flex;
-webkit-flex-direction: row;
        flex-direction: row;
padding: 0px 30px;
cursor: pointer;
}
@media screen and (max-width: 600px) {
  .card-container {
    height: 380px;
    margin-top: 130px;
  }
  .header {
    margin: 0px 0px 0 45px;
    position: fixed;
    background: #fff;
    width: 100%;
    height: 55px;
    padding: 20px 0px 38px 10px;
    z-index: 4;
  }
  .right-menu {
    position: fixed;
    right: 0;
  }
}

.navbar {
  background-color: #fff;
  height: 35px;
  width: 280px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-style: normal;
  font-weight: normal;
  font-size: 40px;
  line-height: 17px;
  background: "none";
}

.nav-menu {
  background-color: #ebebeb;
  width: 280px;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  position: fixed;
  top: 0;
  left: -280px;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  padding: 8px 0px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #a6abb2;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  width: 100%;
  height: 52px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 40px;
}

.nav-text a:hover {
  background-color: #fff;
}

.selected {
  color: #000 !important;
  font-weight: 600 !important;
  background-color: #f5f5f5;
}
.nav-menu-items {
  width: 280px;
}

.navbar-toggle {
  background-color: #e5e5e5;
  width: 280px;
  height: 80px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

span {
  margin-left: 16px;
}

.logo {
  padding: 40px;
}

.active {
  color: #000;
}

.menu-subheading {
  font-family: "Space Grotesk", "sans-serif";
  font-style: normal;
  font-weight: 600;
  font-size: 14px !important;
  line-height: 17px;
  color: #2a2a2a;
  padding: 15px 40px;
}

@media screen and (max-width: 600px) {
  .navbar {
    height: 60px;
    padding-top: 17px;
    position: fixed;
    width: -webkit-fit-content;
  }
}

.not-available {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.na-image {
  width: 45px;
  height: 260px;
  -webkit-transform: rotate(280deg);
          transform: rotate(280deg);
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  opacity: 0.4;
}
.na-heading {
  font-size: 18px;
  color: #3d8f83;
  margin: 30px 0px;
}
.na-body {
  font-size: 20px;
  font-weight: 100;
}

@media screen and (max-width: 600px) {
  .na-image {
    -webkit-transform: rotate(325deg);
            transform: rotate(325deg);
    margin-top: 100px;
  }
}

.card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 176px;
  width: 340px;
  border: 1px solid #ebebeb;
  border-radius: 10px;
  padding: 10px 0px;
}

.left-card {
  border-radius: 10px 0 0 10px;
}

.right-card {
  left: -7px;
  position: relative;
}
.card-header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 18px;
  height: 50px;
}

.card-heading {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */
  color: #2c665d;
}

.card-content {
  padding: 30px 20px;
}
.amount {
  font-family: Space Grotesk;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 43px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  letter-spacing: 0.1px;
  color: #2a2a2a;
}

.divider {
  border: 0.1px solid #eceaeaad;
}

@media screen and (max-width: 600px) {
  .right-card {
    left: 0px;
    position: unset;
    margin-top: 10px;
  }

  .left-card {
    border-radius: 10px;
  }
}

.conversion-rate {
  font-style: normal;
  font-weight: 100;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.1px;
  padding: 5px 0px;
  color: #a6abb2;
}

.grey{
  color: #2A2A2A;
}
.table-container {
  margin-top: 35px;
  padding-left: 10px;
  padding-right: 20px;
}

.responsive-table li {
  border-radius: 3px;
  padding: 10px 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 15px;
  transition: 850ms;
}
.responsive-table .table-row:hover {
  border: 1px solid #2c665d;
  transition: 350ms;
  box-sizing: border-box;
  box-shadow: 0px 2px 30px rgba(41, 40, 40, 0.08);
}
.responsive-table .table-header {
  background-color: #f5f5f5;
  font-size: 16px;
  height: 48px;
  letter-spacing: 0.03em;
}
.responsive-table .table-row {
  background-color: #ffffff;
  max-height: 300px;
  font-weight: 300;
  box-shadow: 0px 1px 0px #ebebeb;
}
.responsive-table .col-4 {
  padding: 7px;
  margin: 5px;
}

.table-header {
  /* font-family: 'Space Grotesk', sans-serif; */
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.1px;
  color: #2a2a2a;
  padding: 7px;
  margin: 5px;
}

.see-more {
  width: 100%;
  height: 60px;
  background: #ffffff;
  border: 1px solid #ebebeb;
  margin-bottom: 40px;
  border-radius: 10px;
  font-size: 18px;
  color: #2c665d;
  font-weight: 600;
}

.no-more {
  margin: 0 auto;
  display: table;
  padding: 20px;
  font-size: 18px;
}
@media all and (max-width: 600px) {
  .table-container {
    display: block;
    margin: 50px auto;
    max-width: 400px;
    width: 380px;
    padding-left: 20px;
    padding-right: 20px;
  }
  .responsive-table .table-header {
    display: none;
  }
  .responsive-table li {
    display: block;
    padding: 5px;
  }
  .responsive-table .col {
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
  }
  .responsive-table .col {
    display: -webkit-flex;
    display: flex;
    padding: 10px 0;
  }
  .responsive-table .col:before {
    color: #6c7a89;
    padding-right: 10px;
    content: attr(data-label);
    -webkit-flex-basis: 30%;
            flex-basis: 30%;
  }
  .responsive-table .table-row {
    box-shadow: 0px 0px 9px 0px rgb(0 0 0 / 10%);
  }
}

.search-wrapper {
